.contacts {
  display: flex;
  flex-direction: column;
  justify-content: start;
  align-items: center;
  min-width: 288px;
  min-height: 169px;
  width: 91vw;
}

.contacts h2 {
  align-self: start;
}

.contacts__mail {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
  min-width: 254px;
  margin-top: 30px;
}

.contacts a {
  min-height: 20px;
  padding-left: 52px;
  font-size: 18px;
  line-height: 18px;
  font-weight: 400;
  text-align: center;
  color: $text_main_color;
  margin: 0;
  background-image: url("../img/icon-mail.svg");
  background-repeat: no-repeat;
  background-position: left center;

  &:hover {
    opacity: 0.7;
  }
}
