.main {
  display: flex;
  flex-direction: column;
  justify-content: start;
  align-items: center;
  width: 100%;
}

.main__heading {
  margin: 15px 0 15px 15px;
  font-size: 24px;
  line-height: 24px;
  font-weight: 400;
  text-align: left;
  color: $text_second_color;
}

.main__section {
  width: 91vw;
  min-width: 288px;
  margin-bottom: 25px;
  border-radius: 5px;
  background-color: $bg_third_color;
}
